'use client';

import React, { useState } from 'react';
import { Container, Title, Text, SimpleGrid, Card, Group, Badge, ThemeIcon, List, Alert, Box, Paper, rem } from '@mantine/core';
import { motion, AnimatePresence } from 'framer-motion';
import { Phone, FileText, Info, MapPin, Clock, AlertCircle, Check } from 'lucide-react';

export default function Guide() {
    const [activeTab, setActiveTab] = useState('usage');

    const tabs = [
        { id: 'usage', label: '이용 안내', icon: <Info size={18} /> },
        { id: 'meeting', label: '회의/행사 문의', icon: <Phone size={18} /> },
        { id: 'facility', label: '부대시설 문의', icon: <MapPin size={18} /> },
    ];

    return (
        <Container size="lg" py="xl">
            <Box mb={50} ta="center">
                <motion.div
                    initial={{ opacity: 0, y: -20 }}
                    animate={{ opacity: 1, y: 0 }}
                    transition={{ duration: 0.5 }}
                >
                    <IconInfoWrapper />
                </motion.div>
                <Title order={1} mb="sm" style={{ fontFamily: 'var(--font-sf)', fontWeight: 800, fontSize: '3rem' }}>
                    이용 안내
                </Title>
                <Text c="dimmed" size="lg" maw={600} mx="auto">
                    방송운영단 시설 이용 및 문의처를 안내해 드립니다.
                </Text>
            </Box>

            {/* Custom Tabs */}
            <Box mb="xl">
                <Group justify="center" gap="sm">
                    {tabs.map((tab) => (
                        <Box
                            key={tab.id}
                            component="button"
                            onClick={() => setActiveTab(tab.id)}
                            style={{
                                position: 'relative',
                                background: 'transparent',
                                border: 'none',
                                cursor: 'pointer',
                                padding: '10px 20px',
                                outline: 'none',
                            }}
                        >
                            <Group gap="xs" style={{ position: 'relative', zIndex: 1 }}>
                                <Text
                                    c={activeTab === tab.id ? 'blue' : 'dimmed'}
                                    fw={activeTab === tab.id ? 700 : 500}
                                    style={{ transition: 'color 0.2s', display: 'flex', alignItems: 'center', gap: '8px' }}
                                >
                                    {tab.icon}
                                    {tab.label}
                                </Text>
                            </Group>
                            {activeTab === tab.id && (
                                <motion.div
                                    layoutId="activeTab"
                                    style={{
                                        position: 'absolute',
                                        bottom: 0,
                                        left: 0,
                                        right: 0,
                                        height: '2px',
                                        background: 'var(--mantine-color-blue-6)',
                                        borderRadius: '2px',
                                    }}
                                />
                            )}
                        </Box>
                    ))}
                </Group>
            </Box>

            <AnimatePresence mode="wait">
                <motion.div
                    key={activeTab}
                    initial={{ opacity: 0, y: 10 }}
                    animate={{ opacity: 1, y: 0 }}
                    exit={{ opacity: 0, y: -10 }}
                    transition={{ duration: 0.2 }}
                >
                    {activeTab === 'usage' && <UsageGuide />}
                    {activeTab === 'meeting' && <MeetingContact />}
                    {activeTab === 'facility' && <FacilityContact />}
                </motion.div>
            </AnimatePresence>
        </Container>
    );
}

function IconInfoWrapper() {
    return <Info size={48} color="#007AFF" style={{ marginBottom: '1rem' }} />;
}

function UsageGuide() {
    return (
        <SimpleGrid cols={{ base: 1, md: 2 }} spacing="xl">
            <Card padding="xl" radius="lg" withBorder>
                <Group mb="md">
                    <ThemeIcon size="xl" radius="md" variant="light" color="blue">
                        <FileText size={20} />
                    </ThemeIcon>
                    <Title order={3}>회의장 이용 절차</Title>
                </Group>

                <SimpleGrid cols={1} spacing="md">
                    <ProcessStep num="01" title="예약 작성" desc="아리오피스 > 회의실 관리 > 회의장 예약 작성" />
                    <ProcessStep num="02" title="신청서 송부" desc="방송사용 신청서 작성 후 담당자에게 송부"
                        detail={
                            <Box mt="xs" p="xs" bg="var(--mantine-color-gray-0)" style={{ borderRadius: '8px' }}>
                                <Text size="sm"><strong>본관:</strong> 정욱종 과장(6011), 최여진 주임(6007)</Text>
                                <Text size="sm"><strong>신관:</strong> 배소영 과장보(4373)</Text>
                            </Box>
                        }
                    />
                    <ProcessStep num="03" title="사전 협의" desc="대강당 대형행사 시 사전협의 필수" />
                </SimpleGrid>

                <Alert icon={<AlertCircle size={16} />} title="유의사항" color="orange" variant="light" mt="xl" radius="md">
                    신청 후 승인된 회의/행사에 한해서만 지원 가능하며, 구두 예약은 방송 지원이 불가합니다.
                </Alert>
            </Card>

            <Box>
                <Card padding="xl" radius="lg" withBorder mb="xl">
                    <Group mb="md">
                        <ThemeIcon size="xl" radius="md" variant="light" color="green">
                            <Clock size={20} />
                        </ThemeIcon>
                        <Title order={3}>방송실 지원 시간</Title>
                    </Group>
                    <List spacing="sm" size="md" center icon={<ThemeIcon color="teal" size={20} radius="xl"><Check size={12} /></ThemeIcon>}>
                        <List.Item>
                            <strong>평일:</strong> 09:00 ~ 12:00 / 13:00 ~ 18:00
                        </List.Item>
                        <List.Item>
                            <strong>휴일:</strong> 부득이한 경우 별도 협의 필요
                        </List.Item>
                        <List.Item>
                            <strong>기타:</strong> 회의 시작 1시간 전 직원 대기, 30분 전 경음악 송출
                        </List.Item>
                    </List>
                </Card>

                <Card padding="xl" radius="lg" withBorder>
                    <Group mb="md">
                        <ThemeIcon size="xl" radius="md" variant="light" color="grape">
                            <Info size={20} />
                        </ThemeIcon>
                        <Title order={3}>기타 안내</Title>
                    </Group>
                    <List spacing="sm" size="md" icon={<ThemeIcon color="grape" size={6} radius="xl" variant="filled" />}>
                        <List.Item>각 회의장별 지원 내용은 "방송장비 이용 가능 현황"을 참고해 주시기 바랍니다.</List.Item>
                        <List.Item>신 화상회의 시스템은 사용자 측에서 직접 운영합니다.</List.Item>
                        <List.Item>
                            문의사항: <Text span c="blue" fw={700}>방송운영단 2080-5994</Text>
                        </List.Item>
                    </List>
                </Card>
            </Box>
        </SimpleGrid>
    );
}

function ProcessStep({ num, title, desc, detail }) {
    return (
        <Group align="flex-start" wrap="nowrap">
            <Text size="xl" fw={900} c="gray.3" style={{ lineHeight: 1 }}>{num}</Text>
            <Box style={{ flex: 1 }}>
                <Text fw={700} size="lg" mb={2}>{title}</Text>
                <Text c="dimmed" size="sm">{desc}</Text>
                {detail}
            </Box>
        </Group>
    );
}

function MeetingContact() {
    return (
        <SimpleGrid cols={{ base: 1, sm: 2, lg: 3 }} spacing="lg">
            <ContactCard title="방송기술 지원" phone="5994" sub="(신관 7112,3)" dept="방송운영단" color="blue" />
            <ContactCard title="중요행사 협의" phone="5993" dept="방송운영단" color="blue" />
            <ContactCard title="좌석 배치" phone="5979" sub="(신관 4397)" dept="용원실" color="cyan" />
            <ContactCard title="이동식 칸막이" phone="5981" dept="건축실" color="cyan" />
            <ContactCard title="DID식순, 화상회의" phone="6011" dept="총무팀" color="teal" />
        </SimpleGrid>
    );
}

function FacilityContact() {
    return (
        <SimpleGrid cols={{ base: 1, sm: 2, lg: 3 }} spacing="lg">
            <ContactCard title="인터넷 설치" phone="6998" sub="(외부망)" dept="헬프데스크" color="indigo" />
            <ContactCard title="인터넷 설치" phone="5995" sub="(내부망)" dept="통신운영실" color="indigo" />
            <ContactCard title="차량출입, 주차안내" phone="5802" sub="(신관 7132)" dept="보안실" color="orange" />
            <ContactCard title="냉난방 공조" phone="5119" sub="(신관 7119)" dept="중앙관제실" color="orange" />
            <ContactCard title="안내방송, 유튜브중계" phone="5733~4" sub="로비대형스크린" dept="농협방송국" color="pink" />
            <ContactCard title="꽃, 화분" phone="5982" dept="조경실" color="green" />
            <ContactCard title="화장실 / 음식물 처리" phone="4156" dept="관리실" color="gray" />
        </SimpleGrid>
    );
}

function ContactCard({ title, phone, sub, dept, color }) {
    return (
        <Card padding="lg" radius="md" withBorder shadow="sm" style={{ height: '100%' }}>
            <Text size="sm" c="dimmed" mb="xs" fw={700}>{title}</Text>
            <Group align="flex-end" gap="xs" mb="md">
                <Text size="xl" fw={800} c={`${color}.7`}>{phone}</Text>
                {sub && <Text size="xs" c="dimmed" mb={4}>{sub}</Text>}
            </Group>
            <Badge variant="light" color={color} fullWidth>{dept}</Badge>
        </Card>
    );
}
